  //     confirmButton: 'btn btn-lg mantasi',
  //     cancelButton: 'btn btn-lg btn-secondary'
  //   },
  //   buttonsStyling: false
  // }) 
  const [loading, setLoading] = useState(false)
  const handleSudah = () =>{
    setLoading(true)
  } 
